
.page1::after {
  content: '';
  width: 100%;
  height: 100%;
  background: url(../../static/img/page1shadow.png) 0 0 no-repeat;
  background-size: cover;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
}
.page1top {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 294rem;
  height: 140rem;
}
.page1bot {
  position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 560rem;
  height: 130rem;
}
.page1con {
  position: absolute;
  top: 135rem;
  z-index: 3;
}
.page1topwave {
  width: 750rem;
  height: 88rem;
}
.page1cup {
  height: 384rem;
}
.page1title {
  height: 350rem;
}
.page1btn {
  height: 156rem;
}
.page1top img,
.page1bot img,
.page1topwave img,
.page1cup img,
.page1title img,
.page1btn img {
  display: block;
  width: 100%;
  height: 100%;
}
.loading {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}
.loading {
  z-index: 4;
  background-color: #fe6c55;
  background: radial-gradient(circle, #ebc630 10%, transparent 10%), radial-gradient(circle, #ebc630 10%, #f4d340 10%) 10px 10px;
  background-size: 40px 40px;
}
.progress {
  width: 500rem;
  height: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #262626;
  padding: 4rem;
  overflow: visible;
  border-radius: 20rem;
  border-top: 1px solid #000;
  border-bottom: 1px solid #7992a8;
}
.progress .progress-bar {
    float: left;
    width: 0;
    height: 100%;
    color: #fff;
    text-align: center;
    background-color: #337ab7;
    transition: width .6s ease;
}
.progress .progress-bar-striped {
    background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent);
    background-size: 40rem 40rem;
}
.progress .progress-bar-warning {
    background-color: #f0ad4e;
}
.progress .progress-bar {
    border-radius: 20rem;
    position: relative;
    -webkit-animation: animate-positive 2s;
            animation: animate-positive 2s;
}
.progress .progress-bar.active {
    -webkit-animation: reverse progress-bar-stripes 0.5s linear infinite, animate-positive 2s;
            animation: reverse progress-bar-stripes 0.5s linear infinite, animate-positive 2s;
}
@-webkit-keyframes progress-bar-stripes {
from {
    background-position: 40rem 0;
}
to {
    background-position: 0 0;
}
}
@keyframes progress-bar-stripes {
from {
    background-position: 40rem 0;
}
to {
    background-position: 0 0;
}
}



