
.page3 {
  background-image: url(../../static/img/page2bg.jpg);
}
.page3 .page3con {
    background: url(../../static/img/page3border.png) 0 0 no-repeat;
    background-size: cover;
    padding-top: 360rem;
    height: 620rem;
    text-align: center;
}
.page3 .textbigger, .page3 .textsmaller {
    color: #fff;
    margin: 0;
    line-height: 1.4;
}
.page3 .textbigger {
    font-size: 60rem;
}
.page3 .textsmaller {
    font-size: 48rem;
    margin-top: 10rem;
}
.page3 .page3btnwrap {
    padding: 20rem 0 0;
    text-align: center;
}
.page3 .btnagain, .page3 .btnshare {
    display: inline-block;
    width: 300rem;
    height: 130rem;
    margin: 0 8rem;
}
.page3 .btnagain img, .page3 .btnshare img {
      display: block;
      width: 100%;
      height: 100%;
}
.page3 .picwrap {
    text-align: center;
    width: 200rem;
    margin: 60rem auto 0;
    height: 228rem;
}
.page3 .picwrap img {
      display: block;
      width: 100%;
      height: 100%;
}
.page3 .sharecover {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
}
.page3 .sharecover img {
      position: absolute;
      width: 350rem;
      height: 210rem;
      top: 0;
      right: 0;
}



